import React, { useState, useEffect } from 'react';
import { adminAPI, licensesAPI } from '../services/api';

const AdminDashboard = () => {
  const [serialNumbers, setSerialNumbers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState(null);
  const [messageType, setMessageType] = useState('');
  const [generateCount, setGenerateCount] = useState(1);

  useEffect(() => {
    loadSerialNumbers();
  }, []);

  const loadSerialNumbers = async () => {
    try {
      const response = await licensesAPI.getAdminSerialNumbers();
      setSerialNumbers(response.data);
    } catch (error) {
      console.error('Failed to load serial numbers:', error);
    }
  };

  const handleGenerateSerials = async (e) => {
    e.preventDefault();
    
    try {
      setLoading(true);
      setMessage(null);
      
      const response = await licensesAPI.generateSerialNumbers(generateCount);
      
      setMessage(`Successfully generated ${response.data.length} serial number(s)!`);
      setMessageType('success');
      setGenerateCount(1);
      
      // Reload the list
      await loadSerialNumbers();
    } catch (error) {
      console.error('Failed to generate serials:', error);
      setMessage(error.response?.data?.detail || 'Failed to generate serial numbers');
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      {/* Generate Serial Numbers */}
      <div className="bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">Generate Serial Numbers</h3>
          <p className="text-sm text-gray-500 mt-1">Create new serial numbers for Perfect Soup</p>
        </div>
        
        <div className="p-6">
          <form onSubmit={handleGenerateSerials} className="space-y-4">
            <div>
              <label htmlFor="count" className="block text-sm font-medium text-gray-700 mb-2">
                Number of serials to generate
              </label>
              <input
                type="number"
                id="count"
                min="1"
                max="100"
                value={generateCount}
                onChange={(e) => setGenerateCount(parseInt(e.target.value) || 1)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500"
                disabled={loading}
              />
            </div>
            
            <button
              type="submit"
              disabled={loading}
              className="px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loading ? 'Generating...' : 'Generate Serial Numbers'}
            </button>
          </form>
          
          {message && (
            <div className={`mt-4 p-4 rounded-md ${
              messageType === 'success' ? 'bg-green-50 text-green-800' :
              messageType === 'error' ? 'bg-red-50 text-red-800' :
              'bg-blue-50 text-blue-800'
            }`}>
              {message}
            </div>
          )}
        </div>
      </div>

      {/* Serial Numbers List */}
      <div className="bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">Serial Numbers</h3>
          <p className="text-sm text-gray-500 mt-1">All generated serial numbers for Perfect Soup</p>
        </div>

        {serialNumbers.length === 0 ? (
          <div className="p-6 text-center">
            <p className="text-gray-500">No serial numbers generated yet</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Serial Number
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Product
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Created
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {serialNumbers.map((serial) => (
                  <tr key={serial.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <code className="text-sm font-mono text-gray-900">{serial.serial}</code>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {serial.product_name}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                        serial.is_used 
                          ? 'bg-green-100 text-green-800' 
                          : 'bg-gray-100 text-gray-800'
                      }`}>
                        {serial.is_used ? 'Used' : 'Available'}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {new Date(serial.created_at).toLocaleDateString()}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default AdminDashboard;
