import React, { useState, useEffect } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import { authAPI } from '../services/api';

const DeviceCodePage = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { isAuthenticated, loading } = useAuth();
  const [userCode, setUserCode] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    // If user is already authenticated, skip to completion
    if (isAuthenticated && !loading) {
      const code = searchParams.get('code');
      if (code) {
        // User is already logged in, complete the device flow immediately
        completeDeviceFlow(code);
        return;
      }
    }

    // Auto-submit if code is provided via URL and user is not authenticated
    const code = searchParams.get('code');
    const auto = searchParams.get('auto');
    
    if (code && auto === 'true' && !isAuthenticated) {
      setUserCode(code);
      // Auto-submit after a short delay
      setTimeout(() => {
        handleSubmit(null, code);
      }, 500);
    }
  }, [searchParams, isAuthenticated, loading]);

  const completeDeviceFlow = async (code) => {
    setIsSubmitting(true);
    setError('');

    try {
      const response = await authAPI.deviceVerify(code.toUpperCase());
      
      if (response.status === 200 && response.data) {
        const { success, auto_completed, session_id } = response.data;
        
        if (success && auto_completed) {
          // User is already authenticated, device flow completed automatically
          navigate('/device-success');
        } else if (success && session_id) {
          // User needs to authenticate, redirect to auth page
          navigate(`/device-auth?session=${session_id}`);
        } else {
          setError('Failed to complete device authentication');
        }
      }
    } catch (error) {
      console.error('Device flow completion error:', error);
      setError('Failed to complete device authentication');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleSubmit = async (e, autoCode = null) => {
    if (e) e.preventDefault();
    
    const codeToSubmit = autoCode || userCode;
    if (!codeToSubmit) {
      setError('Please enter a device code');
      return;
    }

    setIsSubmitting(true);
    setError('');

    try {
      const response = await authAPI.deviceVerify(codeToSubmit.toUpperCase());
      
      // The backend returns JSON with session info
      if (response.status === 200 && response.data) {
        const { success, auto_completed, session_id } = response.data;
        
        if (success && auto_completed) {
          // User is already authenticated, device flow completed automatically
          navigate('/device-success');
        } else if (success && session_id) {
          // Navigate to device-auth page with session ID
          navigate(`/device-auth?session=${session_id}`);
        } else {
          setError('Invalid device code');
        }
      }
    } catch (error) {
      // Handle error responses
      if (error.response && error.response.data) {
        setError(error.response.data.detail || 'Invalid device code');
      } else {
        setError('Network error. Please try again.');
        console.error('Device verification error:', error);
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  const formatUserCode = (value) => {
    // Remove all non-alphanumeric characters and convert to uppercase
    const cleaned = value.replace(/[^A-Z0-9]/g, '').toUpperCase();
    
    // Add hyphen after 4 characters
    if (cleaned.length <= 4) {
      return cleaned;
    } else {
      return cleaned.slice(0, 4) + '-' + cleaned.slice(4, 8);
    }
  };

  const handleCodeChange = (e) => {
    const formatted = formatUserCode(e.target.value);
    setUserCode(formatted);
    setError(''); // Clear error when user types
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center px-4">
      <div className="max-w-md w-full space-y-8">
        {/* Header */}
        <div className="text-center">
          <div className="mx-auto h-16 w-16 bg-blue-600 rounded-full flex items-center justify-center mb-6">
            <svg className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
            </svg>
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Connect Umami Audio</h1>
          <p className="text-gray-600 mb-8">
            Enter the code shown in your desktop application to continue
          </p>
        </div>

        {/* Form */}
        <div className="bg-white rounded-lg shadow-lg p-8">
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label htmlFor="user_code" className="block text-sm font-medium text-gray-700 mb-2">
                Device Code
              </label>
              <input
                type="text"
                id="user_code"
                name="user_code"
                value={userCode}
                onChange={handleCodeChange}
                placeholder="XXXX-XXXX"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-center text-lg font-mono tracking-wider"
                maxLength="9"
                pattern="[A-Z]{4}-[A-Z]{4}"
                required
                disabled={isSubmitting}
                autoComplete="off"
                autoFocus
              />
            </div>

            {error && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg">
                {error}
              </div>
            )}

            <button
              type="submit"
              disabled={isSubmitting || !userCode}
              className="w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {isSubmitting ? (
                <>
                  <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Verifying...
                </>
              ) : (
                'Continue'
              )}
            </button>
          </form>
        </div>

        {/* Help Text */}
        <div className="text-center text-sm text-gray-500 space-y-2">
          <p>
            The device code is displayed in your Umami Audio desktop application.
          </p>
          <p>
            It looks like: <span className="font-mono font-semibold">ABCD-1234</span>
          </p>
        </div>
      </div>
    </div>
  );
};

export default DeviceCodePage;
