import React from 'react';

const QueueStatus = ({ queueStatus, onRefresh }) => {
  return (
    <div className="bg-white rounded-lg shadow">
      <div className="px-6 py-4 border-b border-gray-200 flex justify-between items-center">
        <h2 className="text-xl font-semibold text-gray-900">Queue Status</h2>
        <button
          onClick={onRefresh}
          className="text-blue-600 hover:text-blue-500 text-sm font-medium"
        >
          Refresh
        </button>
      </div>

      <div className="p-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {/* Queue Length */}
          <div className="bg-blue-50 p-4 rounded-lg">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <svg
                  className="h-6 w-6 text-blue-600"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </div>
              <div className="ml-3">
                <p className="text-sm font-medium text-blue-800">Jobs in Queue</p>
                <p className="text-2xl font-bold text-blue-900">
                  {queueStatus.queue_length || 0}
                </p>
              </div>
            </div>
          </div>

          {/* Processing Jobs */}
          <div className="bg-yellow-50 p-4 rounded-lg">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <svg
                  className="h-6 w-6 text-yellow-600"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M13 10V3L4 14h7v7l9-11h-7z"
                  />
                </svg>
              </div>
              <div className="ml-3">
                <p className="text-sm font-medium text-yellow-800">Processing</p>
                <p className="text-2xl font-bold text-yellow-900">
                  {queueStatus.processing_count || 0}
                </p>
              </div>
            </div>
          </div>

          {/* Total Jobs */}
          <div className="bg-green-50 p-4 rounded-lg">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <svg
                  className="h-6 w-6 text-green-600"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z"
                  />
                </svg>
              </div>
              <div className="ml-3">
                <p className="text-sm font-medium text-green-800">Total Active</p>
                <p className="text-2xl font-bold text-green-900">
                  {queueStatus.total_jobs || 0}
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Queue Information */}
        <div className="mt-8">
          <h3 className="text-lg font-medium text-gray-900 mb-4">Queue Information</h3>
          
          <div className="bg-gray-50 p-4 rounded-lg">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
              <div>
                <p className="font-medium text-gray-700">Queue Status</p>
                <p className="text-gray-600">
                  {queueStatus.queue_length === 0 ? 'Empty' : 'Active'}
                </p>
              </div>
              
              <div>
                <p className="font-medium text-gray-700">Average Wait Time</p>
                <p className="text-gray-600">
                  {queueStatus.queue_length === 0 ? 'None' : 'Calculating...'}
                </p>
              </div>
              
              <div>
                <p className="font-medium text-gray-700">Available Workers</p>
                <p className="text-gray-600">1 GPU Worker</p>
              </div>
              
              <div>
                <p className="font-medium text-gray-700">Worker Status</p>
                <p className="text-gray-600">
                  {queueStatus.processing_count > 0 ? 'Busy' : 'Idle'}
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Tips */}
        <div className="mt-6 p-4 bg-blue-50 border border-blue-200 rounded-lg">
          <h4 className="text-sm font-medium text-blue-800 mb-2">💡 Tips</h4>
          <ul className="text-sm text-blue-700 space-y-1">
            <li>• Higher priority jobs are processed first</li>
            <li>• Large files may take longer to process</li>
            <li>• You'll receive real-time updates when your job starts processing</li>
            <li>• Jobs are automatically cancelled after 1 hour of processing</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default QueueStatus;
