import React, { useState, useEffect } from 'react';
import { licensesAPI } from '../services/api';

const LicenseList = () => {
  const [licenses, setLicenses] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [expandedLicense, setExpandedLicense] = useState(null);

  useEffect(() => {
    loadLicenses();
  }, []);

  const loadLicenses = async () => {
    try {
      setLoading(true);
      const response = await licensesAPI.getLicenses();
      setLicenses(response.data);
    } catch (error) {
      console.error('Failed to load licenses:', error);
      setError('Failed to load licenses');
    } finally {
      setLoading(false);
    }
  };

  const toggleExpanded = (licenseId) => {
    setExpandedLicense(expandedLicense === licenseId ? null : licenseId);
  };

  const getLicenseStatusColor = (license) => {
    if (!license.license.is_active) return 'bg-gray-100 text-gray-800';
    if (license.license.is_expired) return 'bg-red-100 text-red-800';
    if (license.license.license_type === 'subscription') return 'bg-blue-100 text-blue-800';
    return 'bg-green-100 text-green-800';
  };

  const getLicenseStatusText = (license) => {
    if (!license.license.is_active) return 'Inactive';
    if (license.license.is_expired) return 'Expired';
    if (license.license.license_type === 'subscription') {
      const expiryDate = new Date(license.license.expires_at);
      return `Subscription (expires ${expiryDate.toLocaleDateString()})`;
    }
    return 'Perpetual';
  };

  if (loading) {
    return (
      <div className="bg-white rounded-lg shadow p-6">
        <div className="animate-pulse">
          <div className="h-4 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="space-y-3">
            <div className="h-4 bg-gray-200 rounded"></div>
            <div className="h-4 bg-gray-200 rounded w-5/6"></div>
            <div className="h-4 bg-gray-200 rounded w-4/6"></div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-white rounded-lg shadow p-6">
        <div className="text-center text-red-600">
          <p>{error}</p>
          <button
            onClick={loadLicenses}
            className="mt-2 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow">
      <div className="px-6 py-4 border-b border-gray-200">
        <h3 className="text-lg font-medium text-gray-900">My Licenses</h3>
        <p className="text-sm text-gray-500 mt-1">Manage your product licenses and activations</p>
      </div>

      {licenses.length === 0 ? (
        <div className="p-6 text-center">
          <div className="text-gray-400 mb-4">
            <svg className="mx-auto h-12 w-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 7a2 2 0 012 2m0 0a2 2 0 012 2m-2-2h-2m-6 0H9m11 0a2 2 0 01-2 2m0 0a2 2 0 01-2 2M7 7a2 2 0 012-2m0 0a2 2 0 012 2m-2 0H9m0 0a2 2 0 01-2 2m0 0a2 2 0 01-2 2m2-2h2m0 0H9" />
            </svg>
          </div>
          <h3 className="text-lg font-medium text-gray-900 mb-2">No licenses found</h3>
          <p className="text-gray-500">You haven't registered any product licenses yet.</p>
        </div>
      ) : (
        <div className="divide-y divide-gray-200">
          {licenses.map((licenseData) => (
            <div key={licenseData.license.id} className="p-6">
              <div className="flex items-center justify-between">
                <div className="flex-1">
                  <div className="flex items-center space-x-3">
                    <h4 className="text-lg font-medium text-gray-900">
                      {licenseData.license.product_name}
                    </h4>
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getLicenseStatusColor(licenseData)}`}>
                      {getLicenseStatusText(licenseData)}
                    </span>
                  </div>
                  <div className="mt-2 text-sm text-gray-500">
                    <p>Serial: {licenseData.license.serial_number}</p>
                    <p>Registered: {new Date(licenseData.license.created_at).toLocaleDateString()}</p>
                  </div>
                </div>
                <button
                  onClick={() => toggleExpanded(licenseData.license.id)}
                  className="ml-4 px-3 py-1 text-sm text-blue-600 hover:text-blue-800"
                >
                  {expandedLicense === licenseData.license.id ? 'Hide Details' : 'Show Details'}
                </button>
              </div>

              {expandedLicense === licenseData.license.id && (
                <div className="mt-4 pt-4 border-t border-gray-200">
                  <h5 className="text-sm font-medium text-gray-900 mb-3">Active Computers</h5>
                  {licenseData.activations.length === 0 ? (
                    <p className="text-sm text-gray-500">No computers activated</p>
                  ) : (
                    <div className="space-y-2">
                      {licenseData.activations.map((activation) => (
                        <div key={activation.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                          <div>
                            <p className="text-sm font-medium text-gray-900">{activation.computer_name}</p>
                            <p className="text-xs text-gray-500">
                              {activation.os_info && `${activation.os_info} • `}
                              Last seen: {activation.last_seen 
                                ? new Date(activation.last_seen).toLocaleDateString()
                                : 'Never'
                              }
                            </p>
                          </div>
                          <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                            activation.is_active ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'
                          }`}>
                            {activation.is_active ? 'Active' : 'Inactive'}
                          </span>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default LicenseList;
