import { useState, useEffect, useCallback } from 'react';
import { io } from 'socket.io-client';
import { useAuth } from './useAuth';
import api from '../services/api';

export const useWebSocket = () => {
  const [socket, setSocket] = useState(null);
  const [connected, setConnected] = useState(false);
  const [jobUpdates, setJobUpdates] = useState([]);
  const [websocketUrl, setWebsocketUrl] = useState(null);
  const { user, isAuthenticated } = useAuth();

  // Fetch WebSocket configuration
  useEffect(() => {
    const fetchConfig = async () => {
      try {
        const response = await api.get('/api/config');
        const config = response.data;
        // Use secure WebSocket URL if available, fallback to regular WebSocket URL
        const wsUrl = config.secure_websocket_url || config.websocket_url;
        setWebsocketUrl(wsUrl);
      } catch (error) {
        console.error('Failed to fetch WebSocket config:', error);
        // Fallback to environment variable or default
        const fallbackUrl = 'wss://udev-backend.teeramusic.com:15351';
        setWebsocketUrl(fallbackUrl);
      }
    };

    fetchConfig();
  }, []);

  useEffect(() => {
    if (websocketUrl) {
      const newSocket = io(websocketUrl, {
        transports: ['websocket', 'polling'],
      });

      newSocket.on('connect', () => {
        console.log('WebSocket connected');
        setConnected(true);
        
        // Join user room for job updates only if authenticated
        if (isAuthenticated && user) {
          newSocket.emit('join_user_room', { user_id: user.id });
        }
      });

      newSocket.on('disconnect', () => {
        console.log('WebSocket disconnected');
        setConnected(false);
      });

      newSocket.on('job_update', (data) => {
        console.log('Job update received:', data);
        setJobUpdates(prev => [data, ...prev.slice(0, 99)]); // Keep last 100 updates
      });

      newSocket.on('system_message', (data) => {
        console.log('System message:', data);
      });

      newSocket.on('email_verified', (data) => {
        console.log('Email verified:', data);
      });

      newSocket.on('joined_verification_room', (data) => {
        console.log('Joined verification room:', data);
      });

      setSocket(newSocket);

      return () => {
        newSocket.close();
      };
    }
  }, [websocketUrl, isAuthenticated, user]);

  const sendMessage = useCallback((event, data) => {
    if (socket && connected) {
      socket.emit(event, data);
    }
  }, [socket, connected]);

  const clearJobUpdates = useCallback(() => {
    setJobUpdates([]);
  }, []);

  return {
    socket,
    connected,
    jobUpdates,
    sendMessage,
    clearJobUpdates,
  };
};
