import axios from 'axios';

const API_BASE_URL = 'https://udev-backend.teeramusic.com:15351';

// Create axios instance
const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor to add auth token
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('access_token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Response interceptor to handle token refresh
api.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config;

    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;

      try {
        const refreshToken = localStorage.getItem('refresh_token');
        if (refreshToken) {
          const response = await axios.post(`${API_BASE_URL}/api/auth/refresh`, {
            refresh_token: refreshToken,
          });

          const { access_token, refresh_token: newRefreshToken } = response.data;
          localStorage.setItem('access_token', access_token);
          localStorage.setItem('refresh_token', newRefreshToken);

          // Retry original request
          originalRequest.headers.Authorization = `Bearer ${access_token}`;
          return api(originalRequest);
        }
      } catch (refreshError) {
        // Refresh failed, redirect to login
        localStorage.removeItem('access_token');
        localStorage.removeItem('refresh_token');
        
        // Don't redirect if we're on the email verification page
        if (!window.location.href.includes('/email-verification')) {
          window.location.href = '/';
        }
      }
    }

    return Promise.reject(error);
  }
);

// Auth API
export const authAPI = {
  register: (userData) => api.post('/api/auth/register', userData),
  login: (credentials) => api.post('/api/auth/login', credentials),
  refreshToken: (refreshToken) => api.post('/api/auth/refresh', { refresh_token: refreshToken }),
  getCurrentUser: () => api.get('/api/me'),
  oauthCallback: (provider, code, state) => api.post(`/api/auth/oauth/${provider}/callback`, { code, state }),
  requestPasswordReset: (email) => api.post('/api/auth/password-reset-request', { email }),
  resetPassword: (token, newPassword) => api.post('/api/auth/password-reset', { token, new_password: newPassword }),
  resendVerification: (email) => api.post('/api/auth/resend-verification', { email }),
  // Device Flow API
  deviceVerify: (userCode) => api.post('/api/auth/device/verify', { user_code: userCode }),
  deviceComplete: (sessionId) => api.post('/api/auth/device/complete', { session_id: sessionId }),
  deviceDeny: (sessionId) => api.post('/api/auth/device/deny', { session_id: sessionId }),
};

// Jobs API
export const jobsAPI = {
  createJob: (jobData) => {
    const formData = new FormData();
    
    if (jobData.file) {
      formData.append('file', jobData.file);
    }
    
    // Append job data as JSON string
    const jobInfo = {
      title: jobData.title,
      description: jobData.description,
      parameters: jobData.parameters,
      priority: jobData.priority || 0
    };
    
    // Convert to form data
    Object.keys(jobInfo).forEach(key => {
      if (jobInfo[key] !== undefined && jobInfo[key] !== null) {
        if (typeof jobInfo[key] === 'object') {
          formData.append(key, JSON.stringify(jobInfo[key]));
        } else {
          formData.append(key, jobInfo[key]);
        }
      }
    });

    return api.post('/api/jobs/', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
  },
  getJobs: (skip = 0, limit = 100) => api.get(`/api/jobs/?skip=${skip}&limit=${limit}`),
  getJob: (jobId) => api.get(`/api/jobs/${jobId}`),
  getJobStatus: (jobId) => api.get(`/api/jobs/${jobId}/status`),
  cancelJob: (jobId) => api.delete(`/api/jobs/${jobId}`),
  getQueueStatus: () => api.get('/api/jobs/queue/status'),
};

// Users API
export const usersAPI = {
  getProfile: () => api.get('/api/users/profile'),
  updateProfile: (profileData) => api.put('/api/users/profile', profileData),
  changePassword: (passwordData) => api.put('/api/users/password', passwordData),
  deleteAccount: () => api.delete('/api/users/account'),
};

// Licenses API
export const licensesAPI = {
  getLicenses: () => api.get('/api/licenses/'),
  registerSerial: (data) => api.post('/api/licenses/register', data),
  generateSerialNumbers: (count) => api.post(`/api/licenses/admin/serial-numbers?count=${count}`),
  getAdminSerialNumbers: () => api.get('/api/licenses/admin/serial-numbers'),
};

// Admin API
export const adminAPI = {
  getProducts: () => api.get('/api/admin/products'),
  createProduct: (productData) => api.post('/api/admin/products', productData),
};

export default api;
