import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useWebSocket } from '../hooks/useWebSocket';
import { useAuth } from '../hooks/useAuth';
import { authAPI } from '../services/api';

const EmailVerificationPage = () => {
  const [timeLeft, setTimeLeft] = useState(600); // 10 minutes in seconds
  const [verificationStatus, setVerificationStatus] = useState('waiting');
  const [message, setMessage] = useState('');
  const [isResending, setIsResending] = useState(false);
  const [resendCooldown, setResendCooldown] = useState(0);
  const location = useLocation();
  const navigate = useNavigate();
  const { socket, connected, sendMessage } = useWebSocket();
  const { isAuthenticated, loading: authLoading, checkAuthStatus } = useAuth();

  // Get email and device flow context from location state (passed from registration)
  const email = location.state?.email || '';
  const deviceFlowSessionId = location.state?.deviceFlowSessionId;

  // Check authentication state and redirect appropriately
  useEffect(() => {
    if (isAuthenticated && !authLoading) {
      console.log('✅ User is authenticated, redirecting...');
      setVerificationStatus('verified');
      
      if (deviceFlowSessionId) {
        // Device flow context - redirect back to device auth page
        setMessage('Email verified successfully! Completing device connection...');
        setTimeout(() => {
          navigate(`/device-auth?session=${deviceFlowSessionId}&oauth_success=true`);
        }, 1000);
      } else {
        // Regular flow - redirect to dashboard
        setMessage('Email verified successfully! Redirecting to dashboard...');
        setTimeout(() => {
          navigate('/dashboard');
        }, 1000);
      }
    }
  }, [isAuthenticated, authLoading, navigate, deviceFlowSessionId]);

  useEffect(() => {
    if (!email) {
      navigate('/');
      return;
    }

    // Join email verification room when WebSocket connects
    if (connected && socket) {
      sendMessage('join_email_verification_room', { email });

      // Listen for email verification success
      socket.on('email_verified', (data) => {
        if (data.email === email) {
          setVerificationStatus('verified');
          
          if (deviceFlowSessionId) {
            // Device flow context
            setMessage('Email verified successfully! Completing device connection...');
          } else {
            // Regular flow
            setMessage('Email verified successfully! Redirecting to dashboard...');
          }
          
          // Store tokens if provided
          if (data.access_token) {
            localStorage.setItem('access_token', data.access_token);
          }
          if (data.refresh_token) {
            localStorage.setItem('refresh_token', data.refresh_token);
          }
          
          // Trigger auth state update and redirect will happen via useEffect
          if (data.access_token && data.refresh_token) {
            checkAuthStatus();
          }
        }
      });

      // Listen for verification room join confirmation
      socket.on('joined_verification_room', (data) => {
        setMessage(`Waiting for email verification. Please check your email at ${data.email}`);
      });
    }

    // Cleanup function
    return () => {
      if (socket) {
        socket.off('email_verified');
        socket.off('joined_verification_room');
        sendMessage('leave_email_verification_room', { email });
      }
    };
  }, [connected, socket, email, navigate, sendMessage, checkAuthStatus]);

  // Countdown timer
  useEffect(() => {
    if (timeLeft > 0 && verificationStatus === 'waiting') {
      const timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
      return () => clearTimeout(timer);
    } else if (timeLeft === 0 && verificationStatus === 'waiting') {
      setVerificationStatus('expired');
      setMessage('WebSocket connection timed out. Your verification link is still valid - please check your email and click the link.');
      if (socket) {
        sendMessage('leave_email_verification_room', { email });
      }
    }
  }, [timeLeft, verificationStatus, socket, email, sendMessage]);

  // Resend cooldown timer
  useEffect(() => {
    if (resendCooldown > 0) {
      const timer = setTimeout(() => setResendCooldown(resendCooldown - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [resendCooldown]);

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const handleResendEmail = async () => {
    if (isResending || resendCooldown > 0) return;
    
    setIsResending(true);
    
    try {
      const response = await authAPI.resendVerification(email);
      
      if (response.data.success) {
        setMessage('Verification email sent! Please check your inbox and spam folder.');
        
        // Start cooldown (60 seconds)
        setResendCooldown(60);
      } else {
        setMessage('Failed to resend verification email. Please try again later.');
      }
    } catch (error) {
      console.error('Resend email error:', error);
      setMessage('Failed to resend verification email. Please try again later.');
    } finally {
      setIsResending(false);
    }
  };

  const getStatusIcon = () => {
    switch (verificationStatus) {
      case 'waiting':
        return (
          <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-blue-600 mx-auto"></div>
        );
      case 'verified':
        return (
          <div className="flex items-center justify-center h-16 w-16 mx-auto bg-green-100 rounded-full">
            <svg className="h-8 w-8 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
            </svg>
          </div>
        );
      case 'expired':
        return (
          <div className="flex items-center justify-center h-16 w-16 mx-auto bg-red-100 rounded-full">
            <svg className="h-8 w-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
        );
      default:
        return null;
    }
  };

  const getStatusColor = () => {
    switch (verificationStatus) {
      case 'verified':
        return 'text-green-600';
      case 'expired':
        return 'text-red-600';
      default:
        return 'text-blue-600';
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
          Email Verification
        </h2>
        <p className="mt-2 text-center text-sm text-gray-600">
          Thank you for signing up!
        </p>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          <div className="text-center">
            {getStatusIcon()}
            
            <h3 className={`mt-4 text-lg font-medium ${getStatusColor()}`}>
              {verificationStatus === 'waiting' && 'Check Your Email'}
              {verificationStatus === 'verified' && 'Email Verified!'}
              {verificationStatus === 'expired' && 'Connection Timed Out'}
            </h3>

            <p className="mt-2 text-sm text-gray-500">
              {message || `We've sent a verification link to ${email}`}
            </p>

            {verificationStatus === 'waiting' && (
              <div className="mt-6">
                <div className="bg-blue-50 border border-blue-200 rounded-md p-4">
                  <div className="flex">
                    <div className="flex-shrink-0">
                      <svg className="h-5 w-5 text-blue-400" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                      </svg>
                    </div>
                    <div className="ml-3">
                      <h3 className="text-sm font-medium text-blue-800">
                        Waiting for verification...
                      </h3>
                      <div className="mt-2 text-sm text-blue-700">
                        <p>
                          Please click the link in your email to verify your account.
                          This page will automatically update when you click the link.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="mt-4">
                  <p className="text-sm text-gray-500">
                    Time remaining: <span className="font-mono font-medium text-red-600">{formatTime(timeLeft)}</span>
                  </p>
                </div>

                <div className="mt-4">
                  <p className="text-xs text-gray-400">
                    WebSocket status: {connected ? (
                      <span className="text-green-600 font-medium">Connected</span>
                    ) : (
                      <span className="text-red-600 font-medium">Disconnected</span>
                    )}
                  </p>
                </div>
              </div>
            )}

            {verificationStatus === 'expired' && (
              <div className="mt-6">
                <div className="bg-blue-50 border border-blue-200 rounded-md p-4 mb-4">
                  <div className="flex">
                    <div className="flex-shrink-0">
                      <svg className="h-5 w-5 text-blue-400" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                      </svg>
                    </div>
                    <div className="ml-3">
                      <h3 className="text-sm font-medium text-blue-800">
                        Your verification link is still valid!
                      </h3>
                      <div className="mt-2 text-sm text-blue-700">
                        <p>
                          The waiting connection timed out, but your email verification link never expires. 
                          Please check your email and click the verification link to complete your registration.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <button
                  onClick={() => navigate('/')}
                  className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                >
                  Back to Home
                </button>
              </div>
            )}

            <div className="mt-6">
              <p className="text-xs text-gray-500">
                Didn't receive the email? Check your spam folder or{' '}
                <button
                  onClick={handleResendEmail}
                  disabled={isResending || resendCooldown > 0}
                  className={`font-medium ${
                    isResending || resendCooldown > 0
                      ? 'text-gray-400 cursor-not-allowed'
                      : 'text-blue-600 hover:text-blue-500 cursor-pointer'
                  }`}
                >
                  {isResending
                    ? 'Sending...'
                    : resendCooldown > 0
                    ? `resend in ${resendCooldown}s`
                    : 'resend verification email'
                  }
                </button>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EmailVerificationPage;
