import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { authAPI } from '../services/api';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Debug state changes
  useEffect(() => {
    console.log('🔍 Auth State Debug - isAuthenticated:', isAuthenticated);
    console.log('🔍 Auth State Debug - loading:', loading);
    console.log('🔍 Auth State Debug - user:', user);
  }, [isAuthenticated, loading, user]);

  const checkAuthStatus = async () => {
    try {
      const token = localStorage.getItem('access_token');
      if (token) {
        const response = await authAPI.getCurrentUser();
        setUser(response.data);
        setIsAuthenticated(true);
      }
    } catch (error) {
      console.error('Auth check failed:', error);
      localStorage.removeItem('access_token');
      localStorage.removeItem('refresh_token');
    } finally {
      setLoading(false);
    }
  };

  const checkOAuthTokens = useCallback(async () => {
    // Check if we're returning from OAuth - using URL search params only (History API)
    const urlParams = new URLSearchParams(window.location.search);
    
    console.log('🔍 OAuth Debug - Current URL:', window.location.href);
    console.log('🔍 OAuth Debug - Search:', window.location.search);
    console.log('🔍 OAuth Debug - URL params:', Object.fromEntries(urlParams));
    
    const oauthSuccess = urlParams.get('oauth_success');
    const oauthError = urlParams.get('oauth_error');
    const emailVerified = urlParams.get('email_verified');
    
    console.log('🔍 OAuth Debug - Success flag:', oauthSuccess);
    console.log('🔍 OAuth Debug - Error flag:', oauthError);
    console.log('🔍 Email Verification Debug - Verified flag:', emailVerified);
    
    if (emailVerified) {
      console.log('✅ Email verification detected, processing tokens...');
      
      // Get email verification tokens from URL parameters
      const accessToken = urlParams.get('access_token');
      const refreshToken = urlParams.get('refresh_token');
      
      console.log('🔍 Email Verification Debug - Access token found:', !!accessToken);
      console.log('🔍 Email Verification Debug - Refresh token found:', !!refreshToken);
      
      if (accessToken && refreshToken) {
        console.log('✅ Email verification tokens found, storing in localStorage...');
        
        // Store tokens in localStorage
        localStorage.setItem('access_token', accessToken);
        localStorage.setItem('refresh_token', refreshToken);
        
        try {
          // Get user info immediately
          console.log('🔍 Email Verification Debug - Fetching user info...');
          const response = await authAPI.getCurrentUser();
          console.log('🔍 Email Verification Debug - User info response:', response.data);
          setUser(response.data);
          setIsAuthenticated(true);
          console.log('✅ Email verification successful, user authenticated');
        } catch (error) {
          console.error('❌ Failed to get user info after email verification:', error);
          localStorage.removeItem('access_token');
          localStorage.removeItem('refresh_token');
        }
        
        // Clean up URL after successful processing
        const cleanPath = window.location.pathname;
        window.history.replaceState({}, document.title, cleanPath);
      }
    } else if (oauthSuccess) {
      console.log('✅ OAuth success detected, processing tokens...');
      
      // Get OAuth tokens from cookies
      const getCookie = (name) => {
        const value = `; ${document.cookie}`;
        const parts = value.split(`; ${name}=`);
        if (parts.length === 2) return parts.pop().split(';').shift();
        return null;
      };
      
      const oauthAccessToken = getCookie('oauth_access_token');
      const oauthRefreshToken = getCookie('oauth_refresh_token');
      
      console.log('🔍 OAuth Debug - Access token found:', !!oauthAccessToken);
      console.log('🔍 OAuth Debug - Refresh token found:', !!oauthRefreshToken);
      console.log('🔍 OAuth Debug - All cookies:', document.cookie);
      
      if (oauthAccessToken && oauthRefreshToken) {
        console.log('✅ OAuth tokens found in cookies, storing in localStorage...');
        
        // Store tokens in localStorage
        localStorage.setItem('access_token', oauthAccessToken);
        localStorage.setItem('refresh_token', oauthRefreshToken);
        
        // Clear OAuth cookies
        document.cookie = 'oauth_access_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;';
        document.cookie = 'oauth_refresh_token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;';
        
        try {
          // Get user info immediately
          console.log('🔍 OAuth Debug - Fetching user info...');
          const response = await authAPI.getCurrentUser();
          console.log('🔍 OAuth Debug - User info response:', response.data);
          setUser(response.data);
          setIsAuthenticated(true);
          console.log('✅ OAuth login successful, user authenticated');
        } catch (error) {
          console.error('❌ Failed to get user info after OAuth:', error);
          localStorage.removeItem('access_token');
          localStorage.removeItem('refresh_token');
        }
        
        // Clean up URL after successful processing
        const cleanPath = window.location.pathname;
        window.history.replaceState({}, document.title, cleanPath);
      }
    } else if (oauthError) {
      console.error('OAuth error:', oauthError);
      // Clean up URL
      const cleanPath = window.location.pathname + window.location.hash.split('?')[0];
      window.history.replaceState({}, document.title, cleanPath);
    }
    
    setLoading(false);
  }, []);

  useEffect(() => {
    console.log('🔍 Auth Debug - useEffect triggered');
    // Check for special flows first (OAuth or email verification)
    const urlParams = new URLSearchParams(window.location.search);
    const oauthSuccess = urlParams.get('oauth_success');
    const emailVerified = urlParams.get('email_verified');
    
    console.log('🔍 Auth Debug - OAuth success in useEffect:', oauthSuccess);
    console.log('🔍 Auth Debug - Email verified in useEffect:', emailVerified);
    
    if (oauthSuccess || emailVerified) {
      console.log('🔍 Auth Debug - Taking special flow path (OAuth or email verification)');
      // Special flow - let checkOAuthTokens handle everything (it now handles both OAuth and email verification)
      checkOAuthTokens();
    } else {
      console.log('🔍 Auth Debug - Taking normal auth check path');
      // Normal flow - check existing auth status
      checkAuthStatus();
    }
  }, [checkOAuthTokens]);

  const login = async (credentials) => {
    try {
      // Clear any existing tokens before attempting login
      localStorage.removeItem('access_token');
      localStorage.removeItem('refresh_token');
      setUser(null);
      setIsAuthenticated(false);
      
      const response = await authAPI.login(credentials);
      
      // Check if this is an inactive user response
      if (response.data.error_type === 'inactive_user') {
        return {
          success: false,
          error_type: 'inactive_user',
          message: response.data.message,
          email: response.data.email,
          verification_required: true
        };
      }
      
      // Check if this is an incorrect password response
      if (response.data.error_type === 'incorrect_password') {
        return {
          success: false,
          error_type: 'incorrect_password',
          message: response.data.message,
          email: response.data.email,
          show_reset_link: response.data.show_reset_link
        };
      }
      
      const { access_token, refresh_token } = response.data;
      
      localStorage.setItem('access_token', access_token);
      localStorage.setItem('refresh_token', refresh_token);
      
      // Get user info
      const userResponse = await authAPI.getCurrentUser();
      setUser(userResponse.data);
      setIsAuthenticated(true);
      
      return { success: true };
    } catch (error) {
      return { 
        success: false, 
        error: error.response?.data?.detail || 'Login failed' 
      };
    }
  };

  const register = async (userData) => {
    try {
      // Clear any existing tokens before attempting registration
      localStorage.removeItem('access_token');
      localStorage.removeItem('refresh_token');
      setUser(null);
      setIsAuthenticated(false);
      
      const response = await authAPI.register(userData);
      
      // Check if email verification is required
      if (response.data.verification_required) {
        return { 
          success: true, 
          verification_required: true,
          email: response.data.email,
          message: response.data.message
        };
      }
      
      // Handle old flow (if tokens are returned directly)
      const { access_token, refresh_token } = response.data;
      
      if (access_token && refresh_token) {
        localStorage.setItem('access_token', access_token);
        localStorage.setItem('refresh_token', refresh_token);
        
        // Get user info
        const userResponse = await authAPI.getCurrentUser();
        setUser(userResponse.data);
        setIsAuthenticated(true);
      }
      
      return { success: true };
    } catch (error) {
      console.error('Registration error:', error.response?.data);
      return { 
        success: false, 
        error: error.response?.data?.detail || 'Registration failed' 
      };
    }
  };

  // PKCE helper functions
  const generateCodeVerifier = () => {
    const array = new Uint8Array(32);
    crypto.getRandomValues(array);
    return btoa(String.fromCharCode.apply(null, array))
      .replace(/\+/g, '-')
      .replace(/\//g, '_')
      .replace(/=/g, '');
  };

  const generateCodeChallenge = async (verifier) => {
    const encoder = new TextEncoder();
    const data = encoder.encode(verifier);
    const digest = await crypto.subtle.digest('SHA-256', data);
    return btoa(String.fromCharCode.apply(null, new Uint8Array(digest)))
      .replace(/\+/g, '-')
      .replace(/\//g, '_')
      .replace(/=/g, '');
  };

  const generateState = () => {
    const array = new Uint8Array(16);
    crypto.getRandomValues(array);
    return btoa(String.fromCharCode.apply(null, array))
      .replace(/\+/g, '-')
      .replace(/\//g, '_')
      .replace(/=/g, '');
  };

  const loginWithOAuth = async (provider, deviceFlowSessionId = null) => {
    try {
      // Generate PKCE parameters for security
      const codeVerifier = generateCodeVerifier();
      const codeChallenge = await generateCodeChallenge(codeVerifier);
      const state = deviceFlowSessionId ? `device_${deviceFlowSessionId}` : generateState();
      
      // Store PKCE parameters for token exchange
      sessionStorage.setItem('oauth_code_verifier', codeVerifier);
      sessionStorage.setItem('oauth_state', state);
      
      // Redirect to OAuth provider
      const baseUrl = process.env.REACT_APP_API_URL || 'https://udev-backend.teeramusic.com:15351';
      const params = new URLSearchParams({
        state: state,
        code_challenge: codeChallenge,
        code_challenge_method: 'S256'
      });
      
      const redirectUrl = `${baseUrl}/api/auth/oauth/${provider}?${params}`;
      
      // Store current location to redirect back after OAuth
      sessionStorage.setItem('oauth_redirect', window.location.pathname);
      
      // Redirect to OAuth provider
      window.location.href = redirectUrl;
      
      // Note: The OAuth flow will complete on the callback page
      // This function will not return in the normal sense
      return { success: true };
    } catch (error) {
      return { 
        success: false, 
        error: error.response?.data?.detail || 'OAuth login failed' 
      };
    }
  };

  const handleOAuthCallback = async (provider, code, state) => {
    try {
      const response = await authAPI.oauthCallback(provider, code, state);
      const { access_token, refresh_token } = response.data;
      
      localStorage.setItem('access_token', access_token);
      localStorage.setItem('refresh_token', refresh_token);
      
      // Get user info
      const userResponse = await authAPI.getCurrentUser();
      setUser(userResponse.data);
      setIsAuthenticated(true);
      
      return { success: true };
    } catch (error) {
      return { 
        success: false, 
        error: error.response?.data?.detail || 'OAuth callback failed' 
      };
    }
  };

  const logout = () => {
    localStorage.removeItem('access_token');
    localStorage.removeItem('refresh_token');
    setUser(null);
    setIsAuthenticated(false);
  };

  const requestPasswordReset = async (email) => {
    try {
      const response = await authAPI.requestPasswordReset(email);
      return {
        success: true,
        message: response.data.message
      };
    } catch (error) {
      return {
        success: false,
        error: error.response?.data?.detail || 'Password reset request failed'
      };
    }
  };

  const resetPassword = async (token, newPassword) => {
    try {
      const response = await authAPI.resetPassword(token, newPassword);
      return {
        success: true,
        message: response.data.message
      };
    } catch (error) {
      return {
        success: false,
        error: error.response?.data?.detail || 'Password reset failed'
      };
    }
  };

  const value = {
    user,
    loading,
    isAuthenticated,
    login,
    register,
    loginWithOAuth,
    handleOAuthCallback,
    logout,
    checkAuthStatus,
    requestPasswordReset,
    resetPassword,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
