import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import { useWebSocket } from '../hooks/useWebSocket';
import { jobsAPI } from '../services/api';
import JobForm from '../components/JobForm';
import JobList from '../components/JobList';
import QueueStatus from '../components/QueueStatus';
import LicenseList from '../components/LicenseList';
import RegisterProducts from '../components/RegisterProducts';
import CalibrationList from '../components/CalibrationList';
import AdminDashboard from '../components/AdminDashboard';

const DashboardPage = () => {
  const { isAuthenticated, user, loading: authLoading } = useAuth();
  const { connected, jobUpdates } = useWebSocket();
  const navigate = useNavigate();
  const [jobs, setJobs] = useState([]);
  const [queueStatus, setQueueStatus] = useState({});
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('licenses');

  // Define callback functions before they're used in useEffect
  const loadJobs = useCallback(async () => {
    try {
      const response = await jobsAPI.getJobs();
      setJobs(response.data);
    } catch (error) {
      console.error('Failed to load jobs:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  const loadQueueStatus = useCallback(async () => {
    try {
      const response = await jobsAPI.getQueueStatus();
      setQueueStatus(response.data);
    } catch (error) {
      console.error('Failed to load queue status:', error);
    }
  }, []);

  // Redirect to login if not authenticated (but wait for OAuth processing)
  useEffect(() => {
    if (!isAuthenticated) {
      // Check if we're in the middle of OAuth flow or email verification (using URL search params)
      const urlParams = new URLSearchParams(window.location.search);
      const oauthSuccess = urlParams.get('oauth_success');
      const emailVerified = urlParams.get('email_verified');
      
      // Don't redirect if we're processing OAuth success, email verification, or if auth is still loading
      if (!oauthSuccess && !emailVerified && !authLoading) {
        navigate('/');
      }
    }
  }, [isAuthenticated, navigate, authLoading]);

  // Load initial data when authenticated
  useEffect(() => {
    if (isAuthenticated) {
      loadJobs();
      loadQueueStatus();
    }
  }, [isAuthenticated, loadJobs, loadQueueStatus]);

  // Set up polling for jobs and queue status when not using WebSocket
  useEffect(() => {
    if (!isAuthenticated) return;

    let jobsInterval, queueInterval;

    // Poll jobs every 10 seconds if WebSocket is not connected
    if (!connected) {
      jobsInterval = setInterval(() => {
        loadJobs();
      }, 10000);

      // Poll queue status every 15 seconds
      queueInterval = setInterval(() => {
        loadQueueStatus();
      }, 15000);
    }

    return () => {
      if (jobsInterval) clearInterval(jobsInterval);
      if (queueInterval) clearInterval(queueInterval);
    };
  }, [isAuthenticated, connected, loadJobs, loadQueueStatus]);

  // Update jobs when WebSocket updates come in
  useEffect(() => {
    if (jobUpdates.length > 0) {
      const latestUpdate = jobUpdates[0];
      setJobs(prevJobs => 
        prevJobs.map(job => 
          job.job_id === latestUpdate.job_id 
            ? { ...job, status: latestUpdate.status, progress: latestUpdate.progress || job.progress }
            : job
        )
      );
    }
  }, [jobUpdates]);

  const handleJobCreated = (newJob) => {
    setJobs(prevJobs => [newJob, ...prevJobs]);
    loadQueueStatus(); // Refresh queue status
  };

  const handleJobCancelled = async (jobId) => {
    try {
      await jobsAPI.cancelJob(jobId);
      setJobs(prevJobs => 
        prevJobs.map(job => 
          job.job_id === jobId ? { ...job, status: 'cancelled' } : job
        )
      );
      loadQueueStatus();
    } catch (error) {
      console.error('Failed to cancel job:', error);
    }
  };

  // Show loading spinner during OAuth processing or auth check
  if (authLoading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading...</p>
        </div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return null; // Will redirect via useEffect
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-600 mt-2">
            Welcome back, {user?.name || user?.email}
          </p>
          
          {/* Connection Status */}
          <div className="mt-4 flex items-center space-x-2">
            <div className={`w-3 h-3 rounded-full ${connected ? 'bg-green-500' : 'bg-red-500'}`}></div>
            <span className="text-sm text-gray-600">
              {connected ? 'Connected to real-time updates' : 'Disconnected from real-time updates'}
            </span>
          </div>
        </div>

        {/* Tabs */}
        <div className="border-b border-gray-200 mb-6">
          <nav className="-mb-px flex space-x-8">
            <button
              onClick={() => setActiveTab('licenses')}
              className={`py-2 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'licenses'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              My Licenses
            </button>
            <button
              onClick={() => setActiveTab('register')}
              className={`py-2 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'register'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Register Products / Subscription
            </button>
            <button
              onClick={() => setActiveTab('calibrations')}
              className={`py-2 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'calibrations'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              My Calibrations
            </button>
            {user?.is_admin && (
              <button
                onClick={() => setActiveTab('admin')}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'admin'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                Admin
              </button>
            )}
          </nav>
        </div>

        {/* Tab Content */}
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          <div className="lg:col-span-3">
            {activeTab === 'licenses' && <LicenseList />}
            
            {activeTab === 'register' && <RegisterProducts />}
            
            {activeTab === 'calibrations' && <CalibrationList />}
            
            {activeTab === 'admin' && user?.is_admin && <AdminDashboard />}
            
            {/* Keep old job functionality for backwards compatibility */}
            {activeTab === 'jobs' && (
              <JobList 
                jobs={jobs} 
                loading={loading} 
                onCancelJob={handleJobCancelled}
                onRefresh={loadJobs}
              />
            )}
            
            {activeTab === 'create' && (
              <JobForm onJobCreated={handleJobCreated} />
            )}
            
            {activeTab === 'queue' && (
              <QueueStatus queueStatus={queueStatus} onRefresh={loadQueueStatus} />
            )}
          </div>

          {/* Sidebar */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4">Account Overview</h3>
              
              <div className="space-y-4">
                <div>
                  <p className="text-sm text-gray-500">Account Type</p>
                  <p className="text-lg font-medium text-gray-900">
                    {user?.is_admin ? 'Administrator' : 'Standard User'}
                  </p>
                </div>
                
                <div>
                  <p className="text-sm text-gray-500">Member Since</p>
                  <p className="text-sm text-gray-900">
                    {user?.created_at ? new Date(user.created_at).toLocaleDateString() : 'N/A'}
                  </p>
                </div>
              </div>
              
              <div className="mt-6 pt-6 border-t border-gray-200">
                <h4 className="text-sm font-medium text-gray-900 mb-2">Quick Actions</h4>
                <div className="space-y-2">
                  <button
                    onClick={() => setActiveTab('register')}
                    className="w-full text-left px-3 py-2 text-sm text-blue-600 hover:bg-blue-50 rounded-md"
                  >
                    Register New Product
                  </button>
                  <a
                    href="/download.html"
                    target="_blank"
                    className="block w-full text-left px-3 py-2 text-sm text-green-600 hover:bg-green-50 rounded-md"
                  >
                    Download Software
                  </a>
                  {user?.is_admin && (
                    <button
                      onClick={() => setActiveTab('admin')}
                      className="w-full text-left px-3 py-2 text-sm text-purple-600 hover:bg-purple-50 rounded-md"
                    >
                      Admin Dashboard
                    </button>
                  )}
                </div>
              </div>
              
              <div className="mt-6 pt-6 border-t border-gray-200">
                <h4 className="text-sm font-medium text-gray-900 mb-2">Support</h4>
                <div className="space-y-2">
                  <a
                    href="#"
                    className="block text-sm text-gray-600 hover:text-gray-900"
                  >
                    Documentation
                  </a>
                  <a
                    href="#"
                    className="block text-sm text-gray-600 hover:text-gray-900"
                  >
                    Contact Support
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;

