import React, { useState } from 'react';
import { useAuth } from '../hooks/useAuth';
import { useNavigate } from 'react-router-dom';
import AuthModal from '../components/AuthModal';

const LandingPage = () => {
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);
  const { isAuthenticated, loading } = useAuth();
  const navigate = useNavigate();

  const openAuthModal = () => setIsAuthModalOpen(true);
  const closeAuthModal = () => setIsAuthModalOpen(false);
  
  const handleDashboardClick = () => {
    navigate('/dashboard');
  };

  const scrollToSection = (sectionId) => (e) => {
    e.preventDefault();
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            {/* Logo */}
            <div className="flex items-center">
              <h1 className="text-2xl font-bold text-gray-900">Umami Audio</h1>
            </div>

            {/* Navigation */}
            <nav className="hidden md:flex space-x-8">
              <button onClick={scrollToSection('hero')} className="text-gray-900 font-medium px-3 py-2 text-sm">Home</button>
              <button onClick={scrollToSection('features')} className="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium">Features</button>
              <button onClick={scrollToSection('how-it-works')} className="text-gray-500 hover:text-gray-900 px-3 py-2 text-sm font-medium">How it Works</button>
            </nav>

            {/* Auth Section */}
            <div className="flex items-center space-x-4">
              {loading ? (
                <div className="w-24 h-10 bg-gray-200 animate-pulse rounded-md"></div>
              ) : isAuthenticated ? (
                <button 
                  onClick={handleDashboardClick}
                  className="bg-blue-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-blue-700 transition-colors"
                >
                  Dashboard
                </button>
              ) : (
                <button 
                  onClick={openAuthModal}
                  className="bg-blue-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-blue-700 transition-colors"
                >
                  Login/Sign Up
                </button>
              )}
            </div>
          </div>
        </div>
      </header>

      {/* Hero Section */}
      <section id="hero" className="min-h-screen bg-gradient-to-br from-blue-600 to-purple-700 flex items-center">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20 text-center text-white">
          <h1 className="text-4xl md:text-6xl font-bold mb-6 animate-fade-in-up">
            Perfect Soup
            <span className="text-yellow-300"> Speaker Calibration</span>
          </h1>
          
          <p className="text-xl mb-8 max-w-3xl mx-auto opacity-90 animate-fade-in-up animation-delay-200">
            ML-powered speaker calibration software that neutralizes room modes, reduces early reflections, 
            manages crossovers, and equalizes speaker response to achieve free field response in any room.
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center animate-fade-in-up animation-delay-400">
            {loading ? (
              <div className="w-32 h-12 bg-white/20 animate-pulse rounded-lg mx-auto"></div>
            ) : isAuthenticated ? (
              <button 
                onClick={handleDashboardClick}
                className="bg-white text-blue-600 px-8 py-3 rounded-lg text-lg font-medium hover:bg-gray-100 transform hover:scale-105 transition-all duration-200"
              >
                Go to Dashboard
              </button>
            ) : (
              <button 
                onClick={openAuthModal}
                className="bg-white text-blue-600 px-8 py-3 rounded-lg text-lg font-medium hover:bg-gray-100 transform hover:scale-105 transition-all duration-200"
              >
                Get Started
              </button>
            )}
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section id="features" className="py-20 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">
              Advanced Audio Features
            </h2>
            <p className="text-lg text-gray-600">
              Professional speaker calibration with cutting-edge ML technology
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-8">
            {/* Feature cards */}
            <div className="bg-gray-50 p-6 rounded-xl hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center mb-4">
                <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 10V3L4 14h7v7l9-11h-7z" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">Room Mode Neutralization</h3>
              <p className="text-gray-600">Advanced ML algorithms neutralize problematic room modes and acoustic anomalies for pristine sound.</p>
            </div>

            <div className="bg-gray-50 p-6 rounded-xl hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center mb-4">
                <svg className="w-6 h-6 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 10V3L4 14h7v7l9-11h-7z" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">Crossover Management</h3>
              <p className="text-gray-600">Intelligent crossover optimization and multi-subwoofer integration for seamless frequency response.</p>
            </div>

            <div className="bg-gray-50 p-6 rounded-xl hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mb-4">
                <svg className="w-6 h-6 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 3v2m6-2v2M9 19v2m6-2v2M5 9H3m2 6H3m18-6h-2m2 6h-2M7 19h10a2 2 0 002-2V7a2 2 0 00-2-2H7a2 2 0 00-2 2v10a2 2 0 002 2zM9 9h6v6H9V9z" />
                </svg>
              </div>
              <h3 className="text-xl font-semibold text-gray-900 mb-2">Free Field Response</h3>
              <p className="text-gray-600">Achieve true free field response in any room environment with precision equalization and reflection control.</p>
            </div>
          </div>
        </div>
      </section>

      {/* How it Works Section */}
      <section id="how-it-works" className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">How It Works</h2>
            <p className="text-lg text-gray-600">Simple steps to calibrate your speakers</p>
          </div>

          <div className="grid md:grid-cols-4 gap-8">
            <div className="text-center">
              <div className="w-16 h-16 bg-blue-600 text-white rounded-full flex items-center justify-center mx-auto mb-4 text-xl font-bold">1</div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Register License</h3>
              <p className="text-gray-600">Create your account and register your Perfect Soup license</p>
            </div>
            <div className="text-center">
              <div className="w-16 h-16 bg-blue-600 text-white rounded-full flex items-center justify-center mx-auto mb-4 text-xl font-bold">2</div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Room Analysis</h3>
              <p className="text-gray-600">Measure your room acoustics and speaker positioning</p>
            </div>
            <div className="text-center">
              <div className="w-16 h-16 bg-blue-600 text-white rounded-full flex items-center justify-center mx-auto mb-4 text-xl font-bold">3</div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">ML Processing</h3>
              <p className="text-gray-600">Our ML algorithms analyze and create optimal calibration settings</p>
            </div>
            <div className="text-center">
              <div className="w-16 h-16 bg-blue-600 text-white rounded-full flex items-center justify-center mx-auto mb-4 text-xl font-bold">4</div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Perfect Sound</h3>
              <p className="text-gray-600">Apply calibration and enjoy pristine free field response</p>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-20 bg-blue-600">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl font-bold text-white mb-4">Ready to perfect your sound?</h2>
          <p className="text-xl text-blue-100 mb-8">Join audio professionals who trust Perfect Soup for pristine speaker calibration</p>
          {loading ? (
            <div className="w-48 h-12 bg-white/20 animate-pulse rounded-lg mx-auto"></div>
          ) : isAuthenticated ? (
            <button 
              onClick={handleDashboardClick}
              className="bg-white text-blue-600 px-8 py-3 rounded-lg text-lg font-medium hover:bg-gray-50 transform hover:scale-105 transition-all duration-200 inline-block"
            >
              Go to Dashboard
            </button>
          ) : (
            <button 
              onClick={openAuthModal}
              className="bg-white text-blue-600 px-8 py-3 rounded-lg text-lg font-medium hover:bg-gray-50 transform hover:scale-105 transition-all duration-200 inline-block"
            >
              Start Calibrating Now
            </button>
          )}
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid md:grid-cols-4 gap-8">
            <div>
              <h3 className="text-lg font-semibold mb-4">Umami Audio</h3>
              <p className="text-gray-400">Professional speaker calibration software for perfect sound reproduction in any environment.</p>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Product</h4>
              <ul className="space-y-2 text-gray-400">
                <li><button onClick={scrollToSection('features')} className="hover:text-white">Features</button></li>
                <li>
                  {isAuthenticated ? (
                    <button onClick={handleDashboardClick} className="hover:text-white">Dashboard</button>
                  ) : (
                    <button onClick={openAuthModal} className="hover:text-white">Dashboard</button>
                  )}
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Support</h4>
              <ul className="space-y-2 text-gray-400">
                <li><a href="#" className="hover:text-white">Documentation</a></li>
                <li><a href="#" className="hover:text-white">API Reference</a></li>
                <li><a href="#" className="hover:text-white">Contact</a></li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Connect</h4>
              <ul className="space-y-2 text-gray-400">
                <li><a href="#" className="hover:text-white">GitHub</a></li>
                <li><a href="#" className="hover:text-white">Twitter</a></li>
                <li><a href="#" className="hover:text-white">Discord</a></li>
              </ul>
            </div>
          </div>
          <div className="border-t border-gray-800 mt-12 pt-8 text-center text-gray-400">
            <p>&copy; 2024 Umami Audio. All rights reserved.</p>
          </div>
        </div>
      </footer>

      {/* Auth Modal */}
      <AuthModal isOpen={isAuthModalOpen} onClose={closeAuthModal} />
    </div>
  );
};

export default LandingPage;
