import React, { useState, useEffect } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import AuthModal from '../components/AuthModal';
import { authAPI } from '../services/api';

const DeviceAuthPage = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { isAuthenticated, loading } = useAuth();
  const [sessionId] = useState(searchParams.get('session'));
  const [isAuthenticating, setIsAuthenticating] = useState(false);
  const [hasCompletedAuth, setHasCompletedAuth] = useState(false);

  useEffect(() => {
    // Verify that we have a valid session ID
    if (!sessionId) {
      navigate('/device');
      return;
    }

    // Prevent multiple calls to handleAuthSuccess
    if (hasCompletedAuth || isAuthenticating) {
      return;
    }

    // Check if we're returning from OAuth success
    const oauthSuccess = searchParams.get('oauth_success');
    
    // If user is already authenticated OR returning from OAuth, complete the device flow immediately
    if ((isAuthenticated && !loading) || oauthSuccess) {
      handleAuthSuccess();
    }
  }, [sessionId, isAuthenticated, loading, searchParams, hasCompletedAuth, isAuthenticating]);

  const handleAuthSuccess = async () => {
    // Prevent duplicate calls
    if (hasCompletedAuth || isAuthenticating) {
      return;
    }
    
    setIsAuthenticating(true);
    setHasCompletedAuth(true);
    
    try {
      // Complete the device authentication
      await authAPI.deviceComplete(parseInt(sessionId));
      
      // Navigate to success page
      navigate('/device-success');
    } catch (error) {
      console.error('Failed to complete device authentication:', error);
      setIsAuthenticating(false);
      setHasCompletedAuth(false); // Reset on error so user can try again
      // Could show an error message here
      alert('Failed to complete device authentication. Please try again.');
    }
  };

  const handleDenyAuth = async () => {
    if (isAuthenticating) {
      return;
    }
    
    setIsAuthenticating(true);
    
    try {
      // Deny the device authentication
      await authAPI.deviceDeny(parseInt(sessionId));
      
      // Navigate back to device page with denial message
      navigate('/device?denied=true');
    } catch (error) {
      console.error('Device denial error:', error);
      navigate('/device?error=denial_failed');
    }
  };

  if (!sessionId) {
    return null; // Will redirect via useEffect
  }

  // Show loading while checking auth status
  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center px-4">
        <div className="text-center">
          <div className="animate-spin mx-auto h-8 w-8 border-4 border-blue-500 border-t-transparent rounded-full mb-4"></div>
          <p className="text-gray-600">Checking authentication status...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center px-4">
      <div className="max-w-md w-full space-y-8">
        {/* Header */}
        <div className="text-center">
          <div className="mx-auto h-16 w-16 bg-green-600 rounded-full flex items-center justify-center mb-6">
            <svg className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Device Code Verified</h1>
          <p className="text-gray-600 mb-8">
            {isAuthenticated 
              ? "Completing connection to your desktop application..."
              : "Please sign in to complete the connection to your desktop application"
            }
          </p>
        </div>

        {/* Authentication Options */}
        <div className="bg-white rounded-lg shadow-lg p-8">
          {isAuthenticating ? (
            <div className="text-center py-8">
              <div className="animate-spin mx-auto h-8 w-8 border-4 border-blue-500 border-t-transparent rounded-full mb-4"></div>
              <p className="text-gray-600">Completing authentication...</p>
            </div>
          ) : isAuthenticated ? (
            <div className="text-center py-8">
              <div className="mx-auto h-12 w-12 bg-green-100 rounded-full flex items-center justify-center mb-4">
                <svg className="h-6 w-6 text-green-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">Already Signed In</h3>
              <p className="text-gray-600 mb-4">You're already authenticated. Completing device connection...</p>
              <button
                onClick={handleAuthSuccess}
                className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition-colors"
              >
                Complete Connection
              </button>
            </div>
          ) : (
            <AuthModal 
              isOpen={true} 
              onClose={() => {
                // Navigate back to device code page if user cancels
                navigate('/device');
              }}
              embedded={true}
              onAuthSuccess={handleAuthSuccess}
              deviceFlowSessionId={sessionId}
            />
          )}
          
          {/* Add deny button for all auth states */}
          {!hasCompletedAuth && (
            <div className="mt-6 text-center">
              <button
                onClick={handleDenyAuth}
                disabled={isAuthenticating}
                className="text-red-600 hover:text-red-800 text-sm underline disabled:opacity-50"
              >
                {isAuthenticating ? 'Processing...' : 'Deny Access'}
              </button>
            </div>
          )}
        </div>

        {/* Info */}
        <div className="text-center text-sm text-gray-500 space-y-2">
          <p>
            After signing in, you'll be able to return to your desktop application.
          </p>
          <p>
            This window will automatically update when authentication is complete.
          </p>
        </div>
      </div>
    </div>
  );
};

export default DeviceAuthPage;
