import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import AuthModal from '../components/AuthModal';

const LoginPage = () => {
  const { isAuthenticated } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    if (isAuthenticated) {
      navigate('/dashboard');
    }
  }, [isAuthenticated, navigate]);

  if (isAuthenticated) {
    return null; // Will redirect via useEffect
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center">
      <div className="max-w-md w-full space-y-8 p-8">
        {/* Header */}
        <div className="text-center">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Welcome to MSO Server</h1>
          <p className="text-gray-600 mb-8">Sign in to access your dashboard</p>
        </div>

        {/* Auth Modal (always open on this page) */}
        <div className="bg-white rounded-lg shadow-lg p-6">
          <AuthModal 
            isOpen={true} 
            onClose={() => {
              // Instead of closing, redirect to home using React Router
              navigate('/');
            }}
            embedded={true}
          />
        </div>

        {/* Links */}
        <div className="text-center">
          <p className="text-sm text-gray-600">
            Don't have an account yet?{' '}
            <span className="text-blue-600">Sign up above</span>
          </p>
          <div className="mt-4">
            <a 
              href="/" 
              className="text-blue-600 hover:text-blue-500 text-sm font-medium"
            >
              ← Back to Home
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
