import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider } from './hooks/useAuth';
import DashboardHeader from './components/DashboardHeader';
import DashboardPage from './pages/DashboardPage';
import LoginPage from './pages/LoginPage';
import LandingPage from './pages/LandingPage';
import EmailVerificationPage from './pages/EmailVerificationPage';
import PasswordResetPage from './pages/PasswordResetPage';
import DeviceCodePage from './pages/DeviceCodePage';
import DeviceAuthPage from './pages/DeviceAuthPage';
import DeviceSuccessPage from './pages/DeviceSuccessPage';
import './App.css';

function App() {
  return (
    <AuthProvider>
      <Router>
        <div className="App min-h-screen bg-gray-50">
          <Routes>
            <Route path="/" element={<LandingPage />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/email-verification" element={<EmailVerificationPage />} />
            <Route path="/reset-password" element={<PasswordResetPage />} />
            <Route path="/device" element={<DeviceCodePage />} />
            <Route path="/device-auth" element={<DeviceAuthPage />} />
            <Route path="/device-success" element={<DeviceSuccessPage />} />
            <Route path="/dashboard" element={
              <>
                <DashboardHeader />
                <DashboardPage />
              </>
            } />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
