import React, { useState } from 'react';
import { licensesAPI } from '../services/api';

const RegisterProducts = () => {
  const [serialNumber, setSerialNumber] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState(null);
  const [messageType, setMessageType] = useState(''); // 'success' or 'error'

  const handleRegisterSerial = async (e) => {
    e.preventDefault();
    
    if (!serialNumber.trim()) {
      setMessage('Please enter a serial number');
      setMessageType('error');
      return;
    }

    try {
      setLoading(true);
      setMessage(null);
      
      const response = await licensesAPI.registerSerial({ serial_number: serialNumber.trim() });
      
      setMessage(`Successfully registered ${response.data.product_name}!`);
      setMessageType('success');
      setSerialNumber('');
    } catch (error) {
      console.error('Failed to register serial:', error);
      setMessage(error.response?.data?.detail || 'Failed to register serial number');
      setMessageType('error');
    } finally {
      setLoading(false);
    }
  };

  const handleSubscription = () => {
    setMessage('Subscription management coming soon!');
    setMessageType('info');
  };

  return (
    <div className="space-y-6">
      {/* Serial Number Registration */}
      <div className="bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">Register Product</h3>
          <p className="text-sm text-gray-500 mt-1">Enter your serial number to register a new product</p>
        </div>
        
        <div className="p-6">
          <form onSubmit={handleRegisterSerial} className="space-y-4">
            <div>
              <label htmlFor="serial" className="block text-sm font-medium text-gray-700 mb-2">
                Serial Number
              </label>
              <input
                type="text"
                id="serial"
                value={serialNumber}
                onChange={(e) => setSerialNumber(e.target.value)}
                placeholder="PS-XXXX-XXXX-XXXX"
                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500"
                disabled={loading}
              />
              <p className="text-xs text-gray-500 mt-1">
                Find your serial number on your product package or receipt
              </p>
            </div>
            
            <button
              type="submit"
              disabled={loading}
              className="w-full px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loading ? 'Registering...' : 'Register Product'}
            </button>
          </form>
          
          {message && (
            <div className={`mt-4 p-4 rounded-md ${
              messageType === 'success' ? 'bg-green-50 text-green-800' :
              messageType === 'error' ? 'bg-red-50 text-red-800' :
              'bg-blue-50 text-blue-800'
            }`}>
              {message}
            </div>
          )}
        </div>
      </div>

      {/* Subscription Management */}
      <div className="bg-white rounded-lg shadow">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">Subscription Management</h3>
          <p className="text-sm text-gray-500 mt-1">Purchase or manage your subscription licenses</p>
        </div>
        
        <div className="p-6">
          <div className="text-center">
            <div className="text-gray-400 mb-4">
              <svg className="mx-auto h-12 w-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h18M7 15h1m4 0h1m-7 4h12a3 3 0 003-3V8a3 3 0 00-3-3H6a3 3 0 00-3 3v8a3 3 0 003 3z" />
              </svg>
            </div>
            <h4 className="text-lg font-medium text-gray-900 mb-2">Subscription Options</h4>
            <p className="text-gray-500 mb-4">
              Get access to Perfect Soup with flexible subscription plans
            </p>
            
            <div className="grid md:grid-cols-2 gap-4 max-w-2xl mx-auto">
              <div className="border border-gray-200 rounded-lg p-6">
                <h5 className="font-medium text-gray-900 mb-2">Monthly</h5>
                <p className="text-2xl font-bold text-gray-900 mb-4">$29<span className="text-sm font-normal text-gray-500">/month</span></p>
                <ul className="text-sm text-gray-500 space-y-1 mb-4">
                  <li>• Full Perfect Soup access</li>
                  <li>• Unlimited calibrations</li>
                  <li>• Email support</li>
                </ul>
                <button
                  onClick={handleSubscription}
                  className="w-full px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700"
                >
                  Subscribe Monthly
                </button>
              </div>
              
              <div className="border border-blue-200 rounded-lg p-6 bg-blue-50">
                <h5 className="font-medium text-gray-900 mb-2">Annual</h5>
                <p className="text-2xl font-bold text-gray-900 mb-4">$299<span className="text-sm font-normal text-gray-500">/year</span></p>
                <ul className="text-sm text-gray-500 space-y-1 mb-4">
                  <li>• Full Perfect Soup access</li>
                  <li>• Unlimited calibrations</li>
                  <li>• Priority email support</li>
                  <li>• 2 months free!</li>
                </ul>
                <button
                  onClick={handleSubscription}
                  className="w-full px-4 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700"
                >
                  Subscribe Annually
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RegisterProducts;
